@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@1,700&display=swap);
@charset 'utf-8';

html {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 14px;
}

body {
  overflow-x: hidden;
  color: rgb(29, 29, 31);
  letter-spacing: -0.05em;
  background: white;
}

.description {
  line-height: 2;
}

a {
  color: rgb(29, 29, 31);
  text-decoration: none;
}

body.before-load {
  overflow: hidden;
}

.container {
  /* iPhone 가로 스크롤 방지 */
  overflow-x: hidden;
}

.scroll-section {
  position: relative;
  /* padding-top: 50vh; */
}

#scroll-section-0 h1 {
  position: relative;
  top: -10vh;
  z-index: 5;
  font-size: 4rem;
  text-align: center;
}

.main-message {
  display: flex;
  align-items: center;
  justify-content: center;
  top: 35vh;
  margin: 5px 0;
  height: 3em;
  font-size: 2.5rem;
  opacity: 0;
}

.main-message p {
  font-weight: bold;
  text-align: center;
  line-height: 1.4;
}

.main-message small {
  display: block;
  margin-bottom: 0.5em;
  font-size: 1.2rem;
}

#scroll-section-2 .main-message {
  font-size: 3.5rem;
}

.description {
  max-width: 1000px;
  /* margin: 0 auto;
  padding: 0 1rem; */
  font-size: 1rem;
  color: #888;
  text-align: start;
}

.description strong {
  float: left;
  margin-right: 0.2em;
  font-size: 3rem;
  color: rgb(29, 29, 31);
  line-height: 1.3;
  margin-bottom: 0.5em;
}

.desc-message {
  width: 50%;
  font-weight: bold;
  opacity: 0;
}
.pin {
  width: 1px;
  height: 100px;
  background: rgb(29, 29, 31);
}

#scroll-section-2 .b {
  top: 10%;
  left: 40%;
}
#scroll-section-2 .c {
  top: 15%;
  left: 45%;
}

.mid-message {
  max-width: 1000px;
  margin: 0 auto;
  /* padding: 0 1rem; */
  padding: 0px 50px 0px 50px;
  font-size: 2rem;
  color: #888;
}

.mid-message strong {
  color: rgb(29, 29, 31);
}

.canvas-caption {
  max-width: 1000px;
  margin: -30rem auto 0;
  text-align: center;
  padding: 0 1rem;
  font-size: 1.2rem;
  color: #888;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 7rem;
}

.sticky-elem {
  display: none;
  position: fixed;
  left: 0;
  width: 100%;
}

#show-scene-0 #scroll-section-0 .sticky-elem,
#show-scene-1 #scroll-section-1 .sticky-elem,
#show-scene-2 #scroll-section-2 .sticky-elem,
#show-scene-3 #scroll-section-3 .sticky-elem {
  display: block;
  will-change: transform, opacity;
}

.sticky-elem-canvas {
  top: 0;
  height: 100%;
}

.sticky-elem-canvas canvas {
  position: absolute;
  top: 50%;
  left: 50%;
}

#scroll-section-3 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-blend-canvas.sticky {
  position: fixed;
  top: 0;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background: white;
  opacity: 0;
  transition: 0.5s;
}

.before-load .container {
  display: none;
}
.before-load .loading {
  opacity: 1;
}

.title-1,
.title-2,
.title-3 {
  font-size: 200px;
  color: #eee;
  text-transform: capitalize;
  background: white;
  background-size: 200% 200%;
  -webkit-background-clip: text;
          background-clip: text;
  font-family: "Roboto Condensed", sans-serif;
}

@keyframes loading-spin {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loading-circle-ani {
  0% {
    stroke-dashoffset: 157;
  }
  75% {
    stroke-dashoffset: -147;
  }
  100% {
    stroke-dashoffset: -157;
  }
}

.loading-circle {
  width: 54px;
  height: 54px;
  animation: loading-spin 3s infinite;
}

.loading-circle circle {
  stroke: black;
  stroke-width: 4;
  /* getTotalLength()로 stroke의 길이를 얻어 올 수 있음 */
  stroke-dasharray: 157;
  stroke-dashoffset: 0;
  fill: none;
  animation: loading-circle-ani 1s infinite;
}

/* .loading-circle:hover circle {
  stroke-dashoffset: -157;
} */

@media (min-width: 1024px) {
  #scroll-section-0 h1 {
    font-size: 6vw;
  }
  .main-message {
    font-size: 4vw;
  }
  .description {
    padding: 0;
    font-size: 1.2rem;
  }
  .description strong {
    font-size: 6rem;
  }
  #scroll-section-2 .main-message {
    font-size: 6vw;
  }
  .main-message small {
    font-size: 1.5vw;
  }
  .desc-message {
    width: 20%;
  }
  #scroll-section-2 .b {
    top: 20%;
    left: 53%;
  }
  #scroll-section-2 .c {
    left: 55%;
  }
  .mid-message {
    width: 1000px;
    padding: 0px 50px 0px 50px;
    font-size: 4vw;
  }
  .canvas-caption {
    margin-top: -8rem;
    padding: 50px 50px 150px 50px;
    font-size: 2rem;
  }

  /* .scroll-section {
    padding-top: 10vh;
  } */

  .scroll-image {
    width: 30vw;
  }
}

/* ///////////////// */

.overlay {
  height: 100%;
  width: 100%;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.active {
  color: white;
}

.slide-container {
  width: 70%;
  margin: 8em auto;
}

.slide-img {
  width: 17vw;
  margin: 0 auto;
}

.slide {
  transform: scale(0.5);
  transition: transform 500ms;
  opacity: 0.5;
  height: 50vh;
}

#scroll-section-3
  > div
  > div
  > div
  > div.slick-list
  > div
  > div:nth-child(6)
  > div {
  height: 80vh;
}

.activeSlide {
  transform: scale(1.1);
  opacity: 1;
  width: 30vw;
  padding-top: 2em;
}

.arrow {
  background-color: #fff;
  position: absolute;
  cursor: pointer;
  z-index: 10;
}

.arrow svg {
  transition: color 300ms;
}

.arrow svg:hover {
  color: gray;
}

.next {
  right: 0%;
  top: 25%;
  background-color: transparent;
}

.prev {
  left: 0%;
  top: 25%;
  background-color: transparent;
}

